@tailwind base;
@tailwind components;
@tailwind utilities;

@layer components {
  .iconHint {
    @apply bg-black/50 text-base text-center font-normal text-white rounded-lg w-[90px] px-[6px] py-[3px];
  }
}
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body {
  height: 100%;
}
.dropdown-menu {
  /* 其他樣式設定省略 */
  position: absolute;
  left: 10px; /* 在滑鼠點擊位置左側 10 像素的位置 */
  top: 10px; /* 在滑鼠點擊位置上方 10 像素的位置 */
}

.dropdown-menu {
  position: absolute;
  top: calc(100% + 40px); /* 距離上方的距離，這裡設定為下拉選單下方 */
  /* 距離左側的距離，這裡設定為與父元素左對齊 */
  width: 85px;
  border-radius: 50%;
}

.dropdown-menu-item {
  padding: 0.5rem;
  background-color: #fff;
  color: #333;
  cursor: pointer;
}

.dropdown-menu-item:hover {
  background-color: #f2f2f2;
}

.dropdown {
  position: relative;
}
.film-icon-container {
  position: relative;
}

.element-class:hover {
  color: rgb(144, 205, 241); /* 更改所需的顏色 */
}


.modal-background {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgb(245, 245, 245, 0.3); 
  backdrop-filter: blur(15px); 
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999;
}

  .modal-content {
    background-color: white;
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    width: 550px; 
    height: 325px; 
    align-items: center; 
    justify-content: center; 
  }
  .modal-content h2 {
    color: rgb(144, 205, 241);
    font-size: 2rem;
    display: flex;
    align-items: center; 
    justify-content: center; 
  }
  
  .unit-buttons {
    display: flex;
    flex-direction: column; 
    font-size: 1.75rem; 
    color:  black; 
  }
  
  .unit-buttons button {
    margin: 15px;
    width: 450px;
  }
  
  .unit-buttons button:hover {
    background-color: #919090 
  }